@media screen and (max-width: 756px) {
  header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: white;
    border-bottom: 1px solid $c-turquoise;
    .logo {
      padding: 1rem 0 1rem 2rem;
      a {
        font-size: 2.8rem; } } }

  //Menu burger button
  .menu-slim {
    display: block;
    background: transparent;
    position: absolute;
    z-index: 555;
    top: 50%;
    transform: translateY(-50%);
    right: 1rem;
    height: 17px;
    width: 50px;
    cursor: pointer;
    &:before,
    &:after {
      background: $c-turquoise;
      backface-visibility: hidden;
      content: "";
      height: 3px;
      left: 0;
      transition: 0.75s;
      width: 30px; }


    &:before {
      box-shadow: $c-turquoise 0 7px 0 0;
      position: absolute;
      top: 0; }


    &:after {
      position: absolute;
      top: calc(100% - 3px); } }


  .menu-js-toggle {
    &:before {
      box-shadow: transparent 0 0 0 0;
      top: 50%;
      transform: rotate(225deg); }

    &:after {
      top: 50%;
      transform: rotate(315deg); } } }



