@media screen and (max-width: 1200px) {
  .caution {
    transform: scaleY(6);
    p {
      font-size: 2rem;
      margin-bottom: 3rem;
      letter-spacing: 0;
      word-spacing: 0; } } }

@media screen and (max-width: 756px) {
  .caution {
    transform: scaleY(4);
    p {
      font-size: 1.5rem;
      margin-top: 10rem;
      word-spacing: 2px;
      letter-spacing: 1px; } } }
@media screen and (max-width: 375px) {
  .caution {
    p {
      letter-spacing: 1.5px;
      line-height: 1;
      padding: 3px; } } }

@media screen and (max-width: 335px) {
  .caution {
    p {
      letter-spacing: 0;
      word-spacing: 5px; } } }
