.indication {
  position: relative;
  &__clipPathBox {
    position: absolute;
    width: 50px;
    height: get-vw(803px);
    top: get-vw(153px);
    right: 0; }
  &-before {
    position: relative;
    max-width: 1500px;
    margin: 0 auto;
    width: 100%;
    img {
      position: absolute;
      left: -210px;
      z-index: -1; } }
  &-after {
    position: absolute;
    height: 100%;
    margin: 0 auto;
    width: 100%;
    img {
      position: absolute;
      width: get-vw(754px);
      height: get-vw(1013px);
      top: 0;
      right: 0;
      z-index: -1; } }

  &-content {
    width: 39%;
    padding-top: 5rem;
    padding-bottom: 10rem;
    color: #fff;
    position: relative;
    ol {
      padding-left: 2.5rem; }
    h2 {
      font-family: Rubik,sans-serif;
      font-size: 4.6rem;
      font-weight: 100; }
    .list__item {
      font-size: 2.4rem;
      font-family: $f-heading;
      font-weight: 400;
      padding-left: 0.9em;
      padding-bottom: 4rem;
      &-sublist {
        list-style-type: none;
        display: flex;
        flex-wrap: wrap;
        padding-left: 2rem;
        padding-top: 1rem;
        width: 450px; }
      .sublist__item {
        width: 210px;
        font-family: $f-regular;
        font-size: 1.8rem;
        padding: 0.5rem 0;
        position: relative;
        opacity: 0.8;
        &:after {
          content: '';
          @extend %vam;
          left: -15px;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background: #fff; } } } }
  &-content2 {
    width: 50%;
    padding-top: 5rem;
    padding-bottom: 10rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto auto auto;
    grid-template-areas: "heading heading" "item--1 item--2" "item--3 item--4" "notice notice";
    @extend %relative;
    h2 {
      font-family: Rubik,sans-serif;
      font-size: 4.6rem;
      font-weight: 100;
      @extend %txtGrad;
      grid-area: heading; }
    p {
      font-size: 1.4rem;
      &:last-child {
        width: 80%;
        grid-area: notice; } }
    .indication-content2__item {
      margin-right: 0.5rem;
      margin-bottom: 3rem;
      img {
        width: 6rem;
        @extend %relative; }
      p {
        font-size: 1.6rem;
        color: #292c35;
        width: 88%; } }
    &--1 {
      grid-area: item--1; }
    &--2 {
      grid-area: item--2; }
    &--3 {
      grid-area: item--3; }
    &--4 {
      grid-area: item--4; } } }
