.usage {
    padding-top: 1em;
    .title {
      margin-top: 7rem; }
    h2 {
        margin-bottom: 30px;
        opacity: 0; }
    .heading {
      margin: 0.7em auto;
      font-size: 2.4rem;
      position: relative;
      width: 87%;
      &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: #deecf6;
        bottom: -1em;
        left: 0; } } }
.usage-wrap {
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    padding: 15px 0 50px 0;
    overflow: hidden;
    & .usage-item {
        flex-basis: 30%;
        background-color: #fff;
        color: #1d1d1d;
        text-align: center;
        margin: 0 2em;
        box-shadow: 0 6px 15px 6px rgba(222,231,237,.3);
        border-radius: 0.5em;
        @extend %relative;
        p {
          font-size: 1.4rem;
          color: #656f86;
          width: 88%;
          margin: 0 auto;
          padding: 2em 0 3em;
          @extend %relative; }
        &:first-of-type {
          .heading {
           line-height: 57px; } }
        span {
           font-weight: bold; }

        img {
          width: 100%;
          height: auto;
          border-top-left-radius: 0.5em;
          border-top-right-radius: 0.5em; } } }


