@media screen and (max-width: 1400px) {
  .storage {
    h2 {
      font-size: 3.6rem; }
    .btn {
      font-size: 1.6rem;
      width: 30rem; } } }
@media screen and (max-width: 992px) {
  .storage {
    .btn {
      margin: 1em; } } }
@media screen and (max-width: 756px) {
  .storage {
    svg {
      width: 40%; }
    .btn-box {
      margin: 50px 0 0 0; }
    p {
      font-size: 1.6rem;
      letter-spacing: 0.5px;
      margin: 0; }
    .btn {
        margin-bottom: 3rem;
        svg {
          right: 1.5em; }
        &-box {
          display: flex;
          justify-content: space-around;
          flex-wrap: wrap; } } } }


@media screen and (max-width: 576px) {
  .storage {
    p {
      font-size: 1.4rem;
      padding: 0 4em; }


    .btn-box {
      margin: 4em 0 0 0;
      .btn {
        width: 29rem;
        font-size: 1.5rem;
        svg {
          width: 25px; } } }

    .btn--instr {
      margin-bottom: 2rem; } } }

@media screen and (max-width: 415px) {
  .storage {
    h2 {
      font-size: 2.6rem; }

    p {
      padding-right: 1em;
      padding-left: 1em;
      line-height: 1.4; }

    svg {
      margin-top: 3rem;
      width: 50%; } } }
