@media screen and (max-width: 1400px) {
  .instruction {
    h2 {
      font-size: 3.6rem; }
    p {
      font-size: 1.4rem; }
    .heading {
      font-size: 1.6rem; }
    &-wrapper {
      .external-box {
        .left-part {
          img {
            width: 75%; } } } }
    &-rectangle {
      top: -35rem;
      left: -1304px; } } }

@media screen and (max-width: 756px) {
  .instruction {
    &-wrapper {
      & .external-box {
          grid-template-columns: 1fr;
          .left-part {
            display: none; }
          .right-part {
            width: 100%;
            margin-bottom: 6rem;
            margin-left: 0;
            text-align: center;
            box-shadow: none; } } } } }

@media screen and (max-width: 415px) {
  .instruction {
    &-wrapper {
      .external-box {
        padding-bottom: 0;
        .right-part {
           padding: 0;
           margin-bottom: 3rem; } } }
    h2 {
      font-size: 2.6rem; }
    .heading {
      font-size: 1.6rem; }
    &__form {
      margin-top: 5rem; } } }



