@media screen and (max-width: 1400px) {
  .description {
    h2 {
      font-size: 3.6rem; }
    &-after {
      img {
        right: -1000px;
        bottom: -100px; } }
    &__benefits {
      .benefits-wrapper {
        grid-row-gap: 3rem;
        h2 {
          font-size: 3.6rem; } } }
    &__list {
      .list-item {
        font-size: 1.6rem;
        background-size: 20px; } } } }
@media screen and (max-width: 1025px) {
  .description {
    h2 {
      font-size: 3rem; }
    ul {
      padding-left: 0;
      .list-item {
        font-size: 1.5rem; } }
    &-wrapper {
      padding-top: 2rem;
      margin-bottom: 5rem;
      text-align: left; }
    &__benefits {
      margin-bottom: 0;
      .benefits-wrapper {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto auto auto;
        grid-template-areas: "heading heading" "item--1 item--2" "item--3 item--3";
        grid-row-gap: 3rem; } } } }

@media screen and (max-width: 756px) {
  .description {
    padding-bottom: 0;
    &-wrapper {
      width: 100%;
      padding-top: 2rem;
      margin-bottom: 5rem;
      text-align: left; }

    &__list {
      width: 50%;
      & .list-item {
        padding: .5em 0 0.5em 2em;
        background-size: 1.5rem; } }
    &-before {
      display: none; }
    &-after {
      display: none; } }


  .description__benefits {
    margin-bottom: 5rem;
    .benefits-wrapper {
      grid-template-columns: 1fr;
      grid-template-rows: auto auto auto auto;
      grid-template-areas: "heading" "item--1"  "item--2"  "item--3";
      grid-row-gap: 4rem; } } }

@media screen and (max-width: 415px) {
  .description {
    padding: 0;
    h2 {
      font-size: 2.6rem; }
    &-wrapper {
      margin-bottom: 5rem; }
    &__list {
      margin-left: 0;
      padding-left: 0.5em;
      &-wrapper {
        width: 90%;
        margin: 0 auto; }
      & .list-item {
        font-size: 1.4rem;
        width: 100%; } }
    .description__benefits {
      .benefits-wrapper {
        grid-row-gap: 2rem;
        h2 {
          font-size: 2.8rem;
          width: 90%;
          margin: 0 auto 3rem; } }
      .benefits__item {
        margin: 0 65px;
        h3 {
          font-weight: 400; }
        img {
          width: 100px;
          margin-bottom: 0; } } } } }

@media screen and (max-width: 335px) {
  .description {
    &__list-wrapper {
      width: 100%; }
    .description__benefits {
      .benefits-wrapper {
        h2 {
          width: 100%; } }
      .benefits__item {
        margin: 0 4rem; } } } }



