footer {
  .author-wrapper {
    display: flex;
    justify-content: space-between;
    svg {
      width: 19%;
      padding: 20px 15px 0 0; } }

  .author__license {
    padding: 4.5em 7.5em 3em 0.5em;
    text-align: left; }
  p {
    color: #656f86;
    font-size: 1.2rem;
    font-weight: 300;
    margin: 0.2em; }
  .author__copyright {
    .container {
      display: flex;
      justify-content: space-between;
      font-size: 1.2rem;
      padding: 1.5em;
      font-weight: 300;
      position: relative;
      overflow: hidden;
      a {
        position: relative;
        color: $c-turquoise;
        font-weight: 500;
        &:after {
          content: "";
          position: absolute;
          right: 0;
          bottom: -3px;
          height: 2px;
          width: 0;
          background-color: #14dfe3;
          transition: width 0.4s ease-in-out; }
        &:hover {
          &:after {
            left: 0;
            width: 100%; } } }
      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #deecf6; } } } }



