@media screen and (max-width: 1400px) {
  .overview {
    padding: 15rem 0 0;
    h1 {
      font-size: 4.5rem; }
    .desktop-img {
      width: 45%; } } }
@media screen and (max-width: 1025px) {
  .overview {
    padding: 10rem 0 0 0;
    .left-part {
      .button-box {
        .btn {
          padding: 1em 2em; } } }
    h1 {
      font-size: 4rem; }
    &-after {
      display: none; } } }
@media screen and (max-width: 756px) {
  .overview {
    padding: 5rem 1rem 3rem;
    text-align: center;
    .container {
      grid-template-areas: 'left left' 'right right';
      justify-items: center; }
    .left-part {
      padding-bottom: 0;
      .button-box {
        .btn {
          width: 29rem;
          font-size: 1.5rem;
          margin-right: 0;
          margin-bottom: 3rem;
          &:last-child {
            margin-bottom: 0; } } } }
    .desktop-img {
      display: none; }
    .mobile-img {
      display: block;
      width: 75%;
      height: auto;
      margin: 0 auto; } } }
@media screen and (max-width: 415px) {
  .overview {
    h1 {
      font-size: 3rem;
      padding: 3rem 0 2rem; }
    p {
      width: 100%; } } }

@media screen and (max-width: 325px) {
  .overview {
    h1 {
      font-size: 2.8rem; }
    p {
      font-size: 1.5rem;
      line-height: 1.5; } } }






