.storage {
  padding-top: 2em;
  text-align: center;
  p {
    font-size: 1.6rem;
    color: #656f86;
    line-height: 2; }
  h2 {
    font-size: 4.6rem;
    font-weight: 100;
    font-family: Rubik,sans-serif; }

  .btn {
    display: inline-block;
    font-size: 1.8rem;
    text-transform: uppercase;
    background: #00a8b8;
    width: 35rem;
    border-radius: 4em;
    padding: 1.23em 0;
    position: relative;
    overflow: hidden;
    margin: 0 30px;
    @extend %relative;
    & span {
      position: absolute;
      padding: 0.75em 0.45em;
      border-radius: 50%;
      background: #fff;
      color: #00a8b8;
      top: 50%;
      right: 0;
      transform: translate(125%, -50%);
      transition: 0.2s linear;
      will-change: transform; }

    &--white {
      background: transparent;
      color: #00a8b8;
      border: 2px solid #00a8b8;
      position: relative;
      transition: background 0.2s linear;
      &:hover {
        background: #00a8b8;
        color: #fff; } }

    &--instr {
      position: relative;
      color: #fff;
      border: 2px solid #00a8b8;
      transition: padding 0.2s linear;
      &:hover {
        padding-right: 35px;
        & span {
          transition: 0.2s linear;
          transform: translate(-15%, -50%);
          opacity: 1;
          visibility: visible; } } } } }

.btn-box {
  margin: 95px 0 170px 0; }




