@media screen and (max-width: 1400px) {
  .indication {
    &-content {
      padding: 2rem;
      width: 50%;
      background-image: url(../img/img-block-2.png);
      background-repeat: no-repeat;
      background-size: cover;
      text-align: center;
      border-radius: 25px;
      h2 {
        font-size: 3.6rem; }
      .list__item {
        font-size: 2rem;
        .sublist__item {
          font-size: 1.5rem; } }
      &2 {
        h2 {
          font-size: 3.6rem; }
        .indication-content2__item {
          img {
            width: 5rem; }
          p {
            font-size: 1.4rem; } } } }
    &-before {
      img {
       display: none; } } } }

@media screen and (max-width: 1025px) {
  .indication {
    &-after {
      display: none; }
    &-before {
      display: none; }
    &__clipPathBox {
      display: none; }
    &-content {
      width: 80%;
      margin: 0 auto;
      background-size: auto; }
    &-content2 {
      width: 80%;
      margin: 0 auto;
      text-align: center;
      padding-bottom: 3rem; } } }
@media screen and (max-width: 756px) {
  .indication {
   .container {
     padding: 0;
     .list__item-sublist {
      width: 100%;
      text-align: left; } }
   &-content {
     width: 100%;
     padding: 2rem;
     border-radius: 0; }
   &-content2 {
     width: 100%;
     text-align: center;
     grid-template-columns: 1fr;
     grid-template-rows: auto auto auto auto;
     grid-template-areas: "heading" "item--1" "item--2" "item--3" "item--4""notice";
     padding: 0 2rem;
     grid-row-gap: 3rem;
     .indication-content2__item {
       margin: 0;
       &--1 {
         margin-left: 1.5rem; }
       &--2 {
         margin-left: 1rem; } }
     p:last-child {
        width: 100%;
        margin-left: auto;
        margin-right: auto; } } } }

@media screen and (max-width: 415px) {
  .indication {
    &-content {
      ol {
        padding-left: 1.5rem; }
      h2 {
        font-size: 2.6rem; }
      .list__item {
        font-size: 1.8rem;
        padding-bottom: 1rem;
        text-align: left;
        .sublist {
          text-align: left;
          &__item {
            font-size: 1.6rem;
            text-align: left; } } } }

    &-content2 {
      padding: 0 1rem;
      margin-top: 3rem;
      h2 {
        font-size: 2.6rem;
        padding: 0; }
      p {
        font-size: 1.2rem;
        width: 100%; } }
    & .indication-content2__item {
      font-size: 1.4rem;
      img {
        width: 5rem; }
      &--1 {
        margin-left: 0;
        img {
          margin-left: 2rem;
          width: 6rem; } } } } }







