.overview {
  padding: 30rem 0 10rem;
  position: relative;
  .desktop-img {
    width: 60%;
    height: auto;
    @extend %relative; }
  .mobile-img {
    display: none; }
  h1 {
    font-family: $f-heading;
    font-size: 5.5rem;
    @extend %txtGrad; }
  p {
    font-size: 1.6rem;
    width: 95%;
    line-height: 2;
    sup {
      color: #14dfe3;
      font-weight: 600;
      cursor: pointer; } }

  .container {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto auto;
    grid-template-areas: 'left right';
    position: relative; }
  .left-part {
    padding-bottom: 7em;
    grid-area: left;
    align-self: center;
    .button-box {
      margin-top: 5rem;
      .btn {
        text-transform: uppercase;
        font-size: 1.4rem;
        font-family: $f-regular;
        font-weight: 500;
        display: inline-block;
        margin-right: 2em;
        padding: 1.1em 3.5em;
        border: 2px solid #00a8b8;
        background: $c-turquoise;
        color: #fff;
        border-radius: 50px;
        @extend %transition;
        &:hover {
          background: transparent;
          color: #00a8b8;
          border: 2px solid #00a8b8; } }
      .btn--white {
        background: transparent;
        color: #00a8b8;
        border: 2px solid #00a8b8;
        position: relative;
        transition: background 0.2s linear;
        &:hover {
          background: #00a8b8;
          color: #fff; } } } }


  .right-part {
    text-align: center;
    align-self: right;
    img {
      width: get-vw(464px)*0.8;
      height: get-vw(547px)*0.8; } }

  &-after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img {
      position: absolute;
      right: get-vw(-517px);
      top: get-vw(-513px);
      will-change: transform;
      transform: rotateZ(15deg);
      z-index: -1;
      width: get-vw(1289px);
      height: get-vw(1289px);
      @extend %relative; }
    &__clipPathBox {
      position: absolute;
      width: 50px;
      top: 0;
      right: 0;
      height: get-vw(825px); } } }

.tooltipster-sidetip.tooltipster-light .tooltipster-content {
    font-size: 14px;
    text-align: center; }

.tooltipster-sidetip.tooltipster-light.tooltipster-top .tooltipster-arrow-background {
  border-top-color: #fff; }

.tooltipster-sidetip.tooltipster-light .tooltipster-box {
  background: #fff; }








