* {
  box-sizing: border-box; }

a {
  text-decoration: none;
  &:-webkit-any-link {
    text-decoration: none; } }
html {
  font-size: 62.5%;
  font-family: $f-regular;

  body {
    position: relative;
    overflow-x: hidden;
    max-width: 1900px;
    margin: 0 auto; } }
.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px; }
h1 {
  font-size: 4.2rem;
  font-weight: bold;
  color: $c-turquoise; }

p {
  font-family: $f-regular;
  color: #7d8691; }

