.instruction {
    position: relative;
    top: 0;
    padding-bottom: 0;
    &-rectangle {
        position: absolute;
        top: 10rem;
        left: -983px;
        width: 100%;
        height: 100%;
        overflow: hidden;
        img {
            position: absolute;
            right: 100px;
            transform: scaleX(1.2) rotateZ(-3deg);
            z-index: -1; } }
    h2 {
        font-family: Rubik,sans-serif;
        font-size: 4.6rem;
        font-weight: 100;
        @extend %txtGrad; }

    &-wrapper {
        padding-bottom: 0;
        overflow-x: hidden;
        & .external-box {
            display: grid;
            grid-template-columns: 50% 50%;
            grid-template-rows: 1fr;
            grid-template-areas: 'left right';
            padding-bottom: 13rem;
            & .left-part {
                grid-area: left;
                justify-self: center;
                margin-left: 5rem;
                img {
                    width: 90%;
                    height: auto;
                    @extend %relative; } }
            & .right-part {
                grid-area: right;
                width: 84%;
                font-size: 1.4rem;
                margin-left: 0;
                padding: 0 3em;
                text-align: left;
                color: #7d8691;
                box-shadow: 0 10px 40px 10px rgba(240,241,242,.5);
                @extend %relative;
                span {
                    font-size: 1.8rem;
                    color: #33495e;
                    display: inline-block;
                    font-weight: 400;
                    margin-bottom: 0.5em; } } } }

    .heading {
        font-family: $f-heading;
        font-size: 1.8rem;
        font-weight: 500;
        color: #33495e;
        text-transform: uppercase;
        margin-bottom: 0; }

    &__form {
        margin-bottom: 2.5em;
        @extend %transition; }

    &__supplementary {
        margin-top: 2em;
        @extend %transition; } }


