.description {
  padding: 5rem 0;
  position: relative;
  &-before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
    img {
      position: absolute;
      left: -974px;
      top: -133px;
      transform: rotateZ(-1deg);
      z-index: -1; } }
  &-after {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;

    img {
      position: absolute;
      right: -756px;
      bottom: -319px;
      transform: scale(.5) rotateZ(-4deg); } }
  &-wrapper {
    width: 50%;
    margin-bottom: 20rem; }
  h2 {
    font-size: 4.6rem;
    font-weight: 100;
    font-family: $f-heading; }
  &__list {
    list-style-type: none;
    padding-left: 0;
    width: 35%;
    &-wrapper {
      display: flex; }
    .list-item {
      font-size: 1.8rem;
      padding: 0.5em 2em;
      color: #7d8691;
      background-image: url(../img/svg/leaf.svg);
      background-repeat: no-repeat;
      background-size: 25px;
      background-position-y: 50%; } } }


.description__benefits {
  margin-bottom: 25rem;
  position: relative;
  .benefits-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto auto;
    grid-template-areas: "heading heading heading" "item--1 item--2 item--3";
    justify-items: center;
    grid-row-gap: 9rem;
    h2 {
      grid-area: heading;
      font-family: $f-heading;
      font-size: 4.6rem;
      font-weight: 100;
      text-align: center;
      width: 70%;
      @extend %txtGrad; } } }
.benefits__item {
    text-align: center;
    margin: 0 90px;
    &:last-child {
      img {
        margin-bottom: 3.5rem; } }
    img {
      width: 100px;
      margin-bottom: 3rem;
      @extend %relative; }

    h3 {
      font-size: 1.4rem;
      text-transform: uppercase; }
    &--1 {
      grid-area: item--1; }
    &--2 {
      grid-area: item--2; }
    &--3 {
      grid-area: item--3; } }
